const requestUrl = 'https://unidemo.dcloud.net.cn/ajax/echo/text?name=uni-app'
const duration = 2000
export default {
	data() {
		return {
			title: 'request',
			loading: false,
			res: ''
		}
	},
	methods: {
		sendRequest: function() {
			this.loading = true
			uni.request({
				url: requestUrl,
				dataType: 'text',
				data: {
					noncestr: Date.now()
				},
				success: (res) => {
					console.log('request success', res)
					uni.showToast({
						title: '请求成功',
						icon: 'success',
						mask: true,
						duration: duration
					});
					this.res = '请求结果 : ' + JSON.stringify(res);
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
					this.loading = false;
				}
			});
		}
	}
}
